jQuery(document).ready(function($) {
    // Handler for Swish QR code button click
    $('.swish-qr-button').on('click', function() {
        // Get the product ID
        var productId = $(this).data('product-id');

        // Send Ajax request to generate Swish QR code
        $.ajax({
            url: ajax_object.ajax_url, // Use the ajax_url provided by WordPress
            method: 'POST',
            data: {
                action: 'generate_swish_qr_code',
                product_id: productId,
                security: ajax_object.ajax_nonce // Pass the nonce for security checks
            },
            success: function(response) {
                // Open lightbox and display the QR code
                // Example: You need to implement the lightbox functionality
                alert('Swish QR code generated. Implement lightbox to display the QR code.');
            },
            error: function(xhr, status, error) {
                console.error(xhr.responseText);
            }
        });
    });
});
